<template>
  <div id="equipment" v-scroll="onScroll">
    <v-card class="mx-0 px-0" elevation="0">
      <v-tabs
        id="tabs"
        :class="{
          sticky: stickyTabs,
          'desktop-sticky': stickyTabs && !commonStore.isMobileMode,
          'mobile-sticky': stickyTabs && commonStore.isMobileMode,
        }"
        class="mx-0"
        v-model="tab"
        color="secondary"
        center-active
        v-bind:show-arrow="!commonStore.isMobileMode"
        v-bind:grow="!commonStore.isMobileMode"
      >
        <template
          v-for="category in props.equipmentCommentList"
          :key="category.categoryCode"
        >
          <v-tab
            :value="category.categoryCode"
            v-show="
              !commonStore.isMobileMode &&
              !(
                props.hideEmpty &&
                category.items.length == 0 &&
                category.cards.length == 0
              )
            "
            @click="onTabChange()"
          >
            {{ category.category }}
          </v-tab>
        </template>
        <v-menu>
          <template v-slot:activator="{ props }">
            <div
              v-if="commonStore.isMobileMode"
              class="align-self-center text-caption font-weight-light ms-5"
            >
              可選擇裝備部位換頁
              <v-icon>mdi-chevron-triple-right</v-icon>
            </div>
            <v-spacer v-if="commonStore.isMobileMode"></v-spacer>
            <v-btn
              v-show="commonStore.isMobileMode"
              variant="flat"
              rounded="0"
              class="align-self-center"
              height="100%"
              prepend-icon="fa:fa-solid fa-compass"
              v-bind="props"
            >
              {{ mobileEquipmentMenuTitle }}
              <v-icon end>mdi-menu-down</v-icon>
            </v-btn>
          </template>

          <v-list class="grey lighten-3">
            <template
              v-for="category in props.equipmentCommentList"
              :key="category.categoryCode"
            >
              <v-list-item
                v-if="
                  !(
                    props.hideEmpty &&
                    category.items.length == 0 &&
                    category.cards.length == 0
                  )
                "
                @click="onTabChange(category.categoryCode)"
              >
                {{ category.category }}
              </v-list-item>
            </template>
          </v-list>
        </v-menu>
      </v-tabs>
      <v-card-text class="mx-0 px-0">
        <v-window v-model="tab">
          <keep-alive
            v-for="category in props.equipmentCommentList"
            :key="category.categoryCode"
          >
            <v-window-item
              reverse-transition="fade-transition"
              transition="fade-transition"
              eager
              v-if="
                !(
                  props.hideEmpty &&
                  category.items.length == 0 &&
                  category.cards.length == 0
                )
              "
              :value="category.categoryCode"
            >
              <v-card
                elevation="0"
                class="mx-0 px-0"
                v-if="!(props.hideEmpty && category.items.length == 0)"
              >
                <v-card-item
                  class="px-0"
                  :id="getRefName(category.categoryCode, false)"
                >
                  <v-card-title
                    class="text-subtitle-1"
                    v-if="category.overrideCategoryName"
                  >
                    <v-icon aria-hidden="false" size="x-small">
                      mdi-chevron-double-right
                    </v-icon>
                    {{ category.category }}
                  </v-card-title>
                  <v-card-title class="text-subtitle-1" v-else>
                    <v-icon aria-hidden="false" size="x-small">
                      mdi-chevron-double-right
                    </v-icon>
                    {{ category.category }}裝備
                    <span class="ps-1 text-caption text-medium-emphasis">
                      {{ category.categorySubTitle }} Equipment
                    </span>
                  </v-card-title>
                </v-card-item>
                <v-card-text class="px-0">
                  <EquipmentComment
                    v-for="item in category.items"
                    :key="item.id"
                    :item="item"
                    :categoryCode="category.categoryCode"
                    :isCard="false"
                    @openDialog="openDialog"
                  ></EquipmentComment>
                </v-card-text>
              </v-card>
              <v-card
                elevation="0"
                class="mx-0 px-0"
                v-if="
                  category.hasCard &&
                  !(props.hideEmpty && category.cards.length == 0)
                "
              >
                <v-card-item
                  class="px-0"
                  :id="getRefName(category.categoryCode, true)"
                >
                  <v-card-title
                    class="text-subtitle-1"
                    v-if="category.overrideCategoryName"
                  >
                    <v-icon aria-hidden="false" size="x-small">
                      mdi-chevron-double-right
                    </v-icon>
                    {{ category.category }}
                  </v-card-title>
                  <v-card-title class="text-subtitle-1" v-else>
                    <v-icon aria-hidden="false" size="x-small">
                      mdi-chevron-double-right
                    </v-icon>
                    {{ category.category }}卡片
                    <span class="ps-1 text-caption text-medium-emphasis">
                      {{ category.categorySubTitle }} Card
                    </span>
                  </v-card-title>
                </v-card-item>
                <v-card-text class="px-0">
                  <EquipmentComment
                    v-for="card in category.cards"
                    :key="card.id"
                    :item="card"
                    :categoryCode="category.categoryCode"
                    :isCard="true"
                    @openDialog="openDialog"
                  ></EquipmentComment>

                  <v-alert
                    v-if="category.hasCard && category.cards.length == 0"
                    density="comfortable"
                    type="info"
                    variant="tonal"
                  >
                    {{ category.noCardMessage }}
                  </v-alert>
                </v-card-text>
              </v-card>
            </v-window-item>
          </keep-alive>
        </v-window>
      </v-card-text>
    </v-card>

    <v-dialog v-model="dialog.open" transition="fade-transition" scrollable>
      <v-card>
        <v-card-title>
          {{ dialog.item.name }}
          {{ getSlotText(dialog.item.slots) }}
        </v-card-title>

        <v-divider></v-divider>
        <v-card-text class="pa-0">
          <template v-for="(info, index) in dialog.item.infos" :key="index">
            <v-card elevation="0">
              <v-card-text>
                <template v-for="(line, index) in info.contents" :key="index">
                  <div v-html="line" v-if="line.length > 0"></div>
                  <br v-else />
                </template>
                <br />
                <div>
                  <div>
                    <span v-if="!isSpecialCategory">
                      系列：{{ info.class }}
                    </span>
                    <span
                      class="ms-5"
                      v-if="!isSpecialCategory && !dialog.isCard"
                    >
                      {{ getValueTitle(dialog.categoryCode) }}：{{ info.value }}
                    </span>
                  </div>
                  <div v-if="!isSpecialCategory">
                    <span v-if="info.position.length > 0">
                      位置：{{ info.position }}
                    </span>
                    <span :class="info.position.length > 0 ? 'ms-5' : ''">
                      重量：{{ info.weight }}
                    </span>
                  </div>
                  <div v-if="!isSpecialCategory && !dialog.isCard">
                    <span>要求等級：{{ info.requiresLevel }}</span>
                  </div>
                  <div v-if="!isSpecialCategory && !dialog.isCard">
                    <span>裝備等級：{{ info.equipmentLevel }}</span>
                  </div>
                  <div v-if="!isSpecialCategory && !dialog.isCard">
                    <span>裝備：{{ info.usableBy }}</span>
                  </div>
                </div>
                <template
                  v-if="
                    !isSpecialCategory &&
                    !dialog.isCard &&
                    info.enchantments.length > 0
                  "
                >
                  <v-divider class="my-3"></v-divider>
                  <div class="text-subtitle-2 font-weight-bold">附魔範例</div>
                  <ul class="main-list ms-4 text-secondary">
                    <li
                      v-for="(enchantment, index) in info.enchantments"
                      :key="index"
                    >
                      {{ enchantment.name }}
                      <ul class="sub-list ms-6 text-primary">
                        <li
                          v-for="(detail, indexDetail) in enchantment.detail"
                          :key="indexDetail"
                        >
                          {{ detail }}
                        </li>
                      </ul>
                    </li>
                  </ul>
                </template>
              </v-card-text>
            </v-card>
          </template>
        </v-card-text>

        <v-card-actions class="d-flex justify-end">
          <v-btn
            block
            size="small"
            color="primary"
            @click="dialog.open = false"
          >
            關閉
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script setup>
import { ref, onUpdated, computed, inject } from 'vue';
import EquipmentComment from './EquipmentComment.vue';
import { useCommonStore } from '@/store/common';

const commonStore = useCommonStore();

const tab = ref(null);
const mobileEquipmentMenuTitle = ref('裝備部位');
const dialog = ref({
  open: false,
  item: {},
  categoryCode: '',
  isCard: false,
});
const stickyTabs = ref(false);
const stickyTriggerY = ref(0);

const smoothScroll = inject('smoothScroll');

const props = defineProps(['equipmentCommentList', 'hideEmpty']);

const isSpecialCategory = computed(
  () =>
    dialog.value.categoryCode == 'costume' || dialog.value.categoryCode == 'pet'
);

onUpdated(() => {
  setDefaultTab();
});

function setDefaultTab() {
  if (tab.value == null) {
    if (props.hideEmpty) {
      const defaultCategory = props.equipmentCommentList.find(
        (c) => c.items.length > 0 || c.cards.length > 0
      );
      tab.value = defaultCategory?.categoryCode;
      mobileEquipmentMenuTitle.value = defaultCategory?.category;
    } else {
      tab.value = props.equipmentCommentList[0]?.categoryCode;
      mobileEquipmentMenuTitle.value = props.equipmentCommentList[0]?.category;
    }
  }
}
function getRefName(categoryCode, isCard) {
  let target = '';
  if (!isCard) {
    target = `${categoryCode}-equipment`;
  } else {
    target = `${categoryCode}-card`;
  }
  return target.replace(/-/gi, '_');
}
function openDialog(item, categoryCode, isCard) {
  dialog.value.item = item;
  dialog.value.categoryCode = categoryCode;
  dialog.value.isCard = isCard;
  dialog.value.open = true;
}
function getSlotText(slots) {
  let text = '';
  if (slots > 0) {
    text = `[${slots}]`;
  }
  return text;
}
function getValueTitle(categoryCode) {
  let title = '';
  switch (categoryCode) {
    case 'weapon':
      title = '攻擊';
      break;

    default:
      title = '防禦';
      break;
  }
  return title;
}
function onScroll() {
  const offsetTop = document.getElementById('tabs').getBoundingClientRect().y;
  if (offsetTop <= 50 && stickyTabs.value == false) {
    stickyTabs.value = true;
    stickyTriggerY.value = window.scrollY;
  }

  if (window.scrollY <= stickyTriggerY.value - 50 && stickyTabs.value == true) {
    stickyTabs.value = false;
  }
}
function onTabChange(categoryCode = '') {
  if (categoryCode != '') {
    tab.value = categoryCode;
  }
  const targetCategory = props.equipmentCommentList.find(
    (c) => c.categoryCode == tab.value
  );
  mobileEquipmentMenuTitle.value = targetCategory.category;
  const el = document.getElementById('equipment');
  smoothScroll({
    scrollTo: el,
    duration: 300,
    offset: -80,
  });
}
</script>
<style scoped>
.btn-info {
  min-width: 50px !important;
  width: 50px !important;
}

.btn-info .icon {
  padding-bottom: 1px !important;
}

.rank {
  padding-top: 1px;
}

.sticky {
  position: fixed;
  top: 50px;
  z-index: 99;
  background-color: white;
}

.desktop-sticky {
  width: calc(100% - 310px);
}

.mobile-sticky {
  width: calc(100% - 55px);
}

#tabs {
  border-style: solid;
  border-width: 1px;
  border-radius: 3px;
  border-color: #cecece;
  opacity: 90%;
}

.v-tabs .v-btn.v-btn--density-default {
  height: initial;
}
</style>
