import { defineStore } from 'pinia';
import axios from 'axios';

export const useGuideStore = defineStore('guide', {
  state: () => {
    return {
      guide: {
        classType: '',
        title: '',
        introduction: [],
        build: {
          skill: {
            content: [],
            info: [],
          },
          state: {
            content: [],
          },
        },
        equipment: {
          content: [],
          comments: [],
        },
        video: {
          content: [],
          urls: [],
        },
      },
    };
  },
  getters: {
    getGuide: (state) => {
      return state.guide;
    },
  },
  actions: {
    async fetchGuide(classType) {
      let responseEquipmentDb = (
        await axios.get(`${process.env.BASE_URL}database/equipments.json`)
      ).data;
      let responseGuideDb = (
        await axios.get(
          `${process.env.BASE_URL}database/guide_${classType}.json`
        )
      ).data;
      let equipmentWithComments = [];
      responseGuideDb.equipment.comments.forEach((category) => {
        const targetCategory = responseEquipmentDb.equipments.find(
          (c) => c.categoryCode == category.categoryCode
        );
        let finalCategory = JSON.parse(JSON.stringify(targetCategory));
        finalCategory.items = [];
        finalCategory.cards = [];

        category.items.forEach((item) => {
          let targetItem = targetCategory.items.find((e) => e.id == item.id);
          targetItem.star = item.star;
          targetItem.comments = item.comments;

          finalCategory.items.push(targetItem);
        });

        category.cards.forEach((card) => {
          //Get card information from DB
          var targetCard = targetCategory.cards.find((c) => c.id == card.id);

          //Add comment and rank
          targetCard.star = card.star;
          targetCard.comments = card.comments;

          //Add to final ouput category
          finalCategory.cards.push(targetCard);
        });
        finalCategory.items.sort((a, b) => b.star - a.star);
        finalCategory.cards.sort((a, b) => b.star - a.star);
        equipmentWithComments.push(finalCategory);
      });

      responseGuideDb.equipment.comments = equipmentWithComments;
      responseGuideDb.classType = classType;
      this.guide = responseGuideDb;
    },
  },
});
